<template>
  <div class="hello">
    <div v-for="(item,index) in todoList" :key="index">
        <van-swipe-cell ref="cellIns" :right-width="130" :left-width="65" :on-close="onClose">
            <span slot="left" class="updateBtn">选择</span>
            <van-cell-group>
                <van-cell v-if="item.case_status==='1'" value=""icon="certificate">
                    <template slot="title">
                        <span class="custom-text">{{item.case_name}}</span>
                    </template>
                </van-cell>
                <van-cell v-else value="" icon="info-o">
                    <template slot="title">
                        <span class="custom-text">{{item.case_name}}</span>
                    </template>
                </van-cell>
            </van-cell-group>
            <div class="editBox" slot="right">
                <span class="delBtn" slot="right" @click="deleteCase(index,item.case_id)">删除</span>
                <span class="delBtn" slot="right" @click="updayeCase(index,item.case_id)">编辑</span>
            </div>
            
        </van-swipe-cell>
    </div>
    <div class="addCase" @click="addCaseMoal"></div>
      <van-dialog
              v-model="show"
              show-cancel-button
              :before-close="beforeClose"
      >
          <span class="addCase-title">添加备忘事件</span>
          <van-field
                  v-model="caseName"
                  placeholder="请输入内容"
          />
          
      </van-dialog>
  </div>
</template>

<script>
  import {interfaces,ajaxReq} from "../modules/request";
  import { Toast } from 'vant'
  export default {
  name: 'HelloWorld',
  data(){
    return {
	    todoList:[],
	    show: false,
	    caseName:''
    }
      },
      methods:{
	      deleteCase(index,id){
	      	 let cellIns=this.$refs['cellIns'][index]
              return new Promise((resolve,reject)=>{
	              this.$dialog.confirm({
		              title: '提示',
		              message: '确认删除？'
	              }).then(() => {
	              	let delParams={
		                caseId:id
                    }
		              ajaxReq.post(interfaces.delCase,delParams).then(res=>{
			              Toast('已删除备忘')
			              this.getAllCase()
                      })
	              }).catch(() => {
		              // on cancel
	              });
              }).then(()=>{
	              cellIns.close()
              })
		      
          },
	      updayeCase(){
            alert('编辑备忘')
          },
	      onClose(clickPosition, instance) {
		      switch (clickPosition) {
			      case 'left':
                      alert(11111)
				      break;
			      case 'cell':
			      case 'outside':
				      instance.close();
				      break;
			      // case 'right':
				  //     this.deleteCase()
				  //     break;
		      }
	      },
	      addCaseMoal(){
	      	this.show=true
          },
	      addCase(){
	      	if(!this.caseName){
		        Toast.fail('失败');
            }else{
		        let addParam={
			        caseName:this.caseName
		        }
		        ajaxReq.post(interfaces.addCase,addParam).then(res=>{
			        console.log(res)
			        if(res.data.code===200){
				        Toast.success('添加成功')
				        this.getAllCase()
				        this.show=false
			        }
		        })
            }
	      	
	      },
	      beforeClose(action,done) {
		      if (action === 'confirm') {
                  if(this.caseName){
	                  this.addCase()
                  }else{
	                  Toast.fail('请添加内容');
                  }
			      setTimeout(done, 1000);
		      } else {
			      done()
			      this.show=false
		      }
	      },
          getAllCase(){
	          ajaxReq.get(interfaces.getAllcase).then(res=>{
		          this.todoList=res.data.data
	          })
          }
      },
      // computed:{
  	//     isComplated(){
  	//
      //   }
      // },
  props: {
    msg: String
  },
      mounted(){
	      this.getAllCase()
      }
}
</script>

