      display: inline-block;
      background: red;
  }
  .delBtn{
      width: 1.6rem;
      height: 1rem;
      line-height: 1rem;
      display: inline-block;
      background: red;
  }
  .addCase-title{
      display: inline-block;
      margin: r(30) 0 0 0;
  }
  .addCase{
    width: r(70);
    height: r(70);
    background: url("../assets/addCase.svg") no-repeat 0 0;
    background-size: contain;
    position: fixed;
    right:0;
    top:60%;
  }
